.footer {
  width: 100%;
  background-color: var(--gray);
  padding: 24px 20px;
  display: flex;
}

.footer__contacts {
  display: flex;
  align-items: center;
  gap: 20px;
  width: calc(50% - 160px);
}

.footer__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}
